/*  COLORS
  ============================== */
/*  CONTROLS
  ============================== */
.App:not(.isTouch) .control {
  display: none; }

.control {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%; }
  .control .top {
    display: flex;
    flex-direction: row; }
  .control .top > div,
  .control .bottom > div {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 72px;
    margin: 16px;
    font-size: 36px;
    font-weight: bold;
    background-color: #e8899e;
    cursor: pointer;
    transition: background-color .25s ease; }
    .control .top > div:hover, .control .top > div:focus, .control .top > div:active,
    .control .bottom > div:hover,
    .control .bottom > div:focus,
    .control .bottom > div:active {
      background-color: #de5c6e; }
    .control .top > div::selection,
    .control .bottom > div::selection {
      background-color: transparent; }
  .control .bottom {
    width: 304px; }
    .control .bottom > div {
      width: 100%;
      margin: 0; }
  .control > div > div:first-child {
    margin-left: 0; }
  .control > div > div:last-child {
    margin-right: 0; }

/*  TETROMINOES
  ============================== */
.I {
  background-color: #ff9705; }

.J {
  background-color: #3434f4; }

.L {
  background-color: #b300b2; }

.O {
  background-color: #ed2121; }

.S {
  background-color: #0090ad; }

.T {
  background-color: #fff200; }

.Z {
  background-color: #008f00; }

/*  LAYOUT
  ============================== */
* {
  box-sizing: border-box; }
  *::selection {
    background-color: rgba(232, 137, 158, 0.99); }

img::selection {
  background-color: #e8899e; }

@font-face {
  font-family: 'munro';
  src: url("../assets/fonts/munro.ttf") format("truetype"); }

html {
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 568px; }

body,
#root,
.App {
  min-height: 100%;
  height: 100%; }

body {
  margin: 0;
  font-family: munro, 'Courier New', Courier, monaco, monospace, sans-serif;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2f2f2f; }

#root {
  display: flex;
  justify-content: center;
  align-items: flex-start; }

.App {
  display: flex;
  height: auto; }

/*  COMMONS - BTN
  ============================== */
.btn {
  display: inline-block;
  padding: 8px 32px 8px 40px;
  font-size: 36px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
  background-color: #e8899e;
  cursor: pointer;
  transition: background-color .25s ease; }
  .btn:hover, .btn:focus, .btn:active {
    background-color: #de5c6e; }

/*  COMMONS - TABLES
  ============================== */
.next-shape-container,
.playfield,
.entrance > .table {
  background-color: #bfe7f2;
  border: 10px solid #e8899e; }

.playfield > div,
.entrance > .table > div {
  float: left; }

.playfield br,
.entrance > .table br {
  content: '';
  display: block; }

.next-shape .line > div,
.playfield > div {
  width: 48px;
  border: 1px solid #e8899e; }

/*  COMMONS - SIDES
  ============================== */
.side {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  font-size: 24px; }

/*  RESP
  ============================== */
.isTouch .side {
  display: none; }

@media screen and (min-width: 1024px) {
  .App:not(.isTouch) .side {
    font-size: 32px; } }

/*  HELPERS
  ============================== */
.d-none,
.border {
  display: none; }
