/*  ENTRANCE
  ============================== */
.entrance {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 32px;
  font-size: 26px;
  background-color: #2f2f2f; }
  .entrance > img {
    width: 240px;
    max-width: 60%; }
  .entrance p {
    text-align: center;
    margin: 32px 0; }
  .entrance > .table > div {
    width: 24px;
    border: 1px solid #e8899e; }
  .entrance > .table.top > div {
    max-width: calc(100% / 23);
    padding-bottom: calc(100% / 23); }
  .entrance > .table.bottom {
    position: relative;
    max-width: calc((100% * 7 / 23) + 14px);
    margin-top: -10px;
    border-top: 0; }
    .entrance > .table.bottom > div {
      max-width: calc(100% / 7);
      padding-bottom: calc(100% / 7); }
    .entrance > .table.bottom > img {
      position: absolute;
      width: 80%;
      top: 10%;
      left: 10%;
      animation: rotate 3s cubic-bezier(0.5, 0, 0.5, 1); }

@keyframes rotate {
  100% {
    -webkit-transform: rotate(540deg);
    transform: rotate(540deg); } }

@media screen and (min-width: 360px) {
  .entrance {
    font-size: 28px; } }
