  top: 0;
  bottom: 0;
  min-width: 100%;
  max-width: 100%;
  min-height: 100%;
  padding: 40px 32px;
  background-color: rgba(0, 0, 0, 0.7);
  overflow: auto; }

.popin {
  width: 450px;
  text-align: center;
